<template>
  <div>
    <select v-model="currentLanguage">
      <option
        v-for="language in languages"
        :value="language.iso_code"
        :key="language.iso_code"
      >
        {{language.name}}
      </option>
    </select>
    <ul>
      <li
        v-for="(form, index) in forms"
        :key="index"
      >
        <router-link :to="`/${currentLanguage}/form/${form.id}`"> {{ form.name }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    forms: [],
    languages: [],
    currentLanguage: "en"
  }),
  created() {
    axios
      .get(
        `${location.protocol}//${location.hostname}${
          process.env.NODE_ENV === "production" ? "" : ":3001"
        }/forms`
      )
      .then(response => (this.forms = response.data));
    axios
      .get(
        `${location.protocol}//${location.hostname}${
          process.env.NODE_ENV === "production" ? "" : ":3001"
        }/languages`
      )
      .then(response => {
        console.log(response.data);
        this.languages = response.data;
      });
  },
  watch: {},
  methods: {}
};
</script>