import Vue from "vue";
import Router from "vue-router";
import { Form, Forms } from "./views";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.NODE_ENV === "production" ? "/public" : "/",
  routes: [
    {
      path: "/",
      name: "forms",
      component: Forms
    },
    {
      path: "/:lang/form/:id",
      name: "form",
      component: Form
    }
  ]
});
