import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import axios from "./myAxios";

Vue.config.productionTip = false;

axios
  .post("/permissions")
  .then(response => {
    if (!response.data.status) {
      localStorage.basicToken = "";
      if (process.env.NODE_ENV === "production") {
        location.href = "/login";
      }
    } else {
      new Vue({
        router,
        render: h => h(App)
      }).$mount("#app");
    }
  })
  .catch(() => {
    if (process.env.NODE_ENV === "production") {
      location.href = "/login";
    }
  });
