<template>
  <div>
    <form
      id="appform"
      @submit="sendForm"
    >
      <select
        style="display: none"
        @change="changeLang"
        v-model="currentLanguage"
      >
        <option
          v-for="language in languages"
          :value="language.iso_code"
          :key="language.iso_code"
        >
          {{language.name}}
        </option>
      </select>
      <div class="grey">
        <h2>CCR Europe EH&amp;S Job Site Inspection</h2>
      </div>
      <div class="fcw">
        <div class=fc><label>Date:</label><input
            v-model="formData.date"
            type="date"
          ></div>
        <div class=fc><label>Location:</label><input v-model="formData.location"></div>
        <div class=fc><label>Technician: </label><input v-model="formData.technician"></div>
        <div class=fc><label>Customer: </label><input v-model="formData.customer"></div>
      </div>
      <table>
        <template v-for="(section, index) in formSection">
          <tr
            @click="open(section.id)"
            class="grey"
            v-bind:class="{ green: isValidSection(section.id) }"
            :key="index"
          >
            <th>{{ section.index }}</th>
            <th colspan="2">{{ section.name }}</th>
          </tr>
          <template v-for="question in section.questions">
            <tr
              v-if="isOpen(section.id)"
              v-bind:class="section.index"
              :key="section.index + '.' + question.index"
            >
              <td>{{ section.index }}.{{ question.index }}</td>
              <td>{{ question.text }}</td>
              <td>
                <div
                  class="control"
                  v-bind:class="{ green: formData.questions[question.id] }"
                >
                  <label
                    class="radio"
                    v-bind:section="section.id"
                  >
                    <input
                      v-model="formData.questions[question.id]"
                      v-bind:name="question.id"
                      type="radio"
                      value="Yes"
                    >
                    Yes
                  </label>
                  <label class="radio">
                    <input
                      v-model="formData.questions[question.id]"
                      v-bind:name="question.id"
                      type="radio"
                      value="No"
                    >
                    No
                  </label>
                </div>
              </td>
            </tr>
            <tr
              v-if="isOpen(section.id) && isOpenFinding(question.id)"
              v-bind:class="section.index"
              :key="'findings-'+section.index + '.' + question.index"
            >
              <td colspan="3">
                <div class="fcv">
                  <div class="fc">
                    <label>Root cause and corrective actions:</label>
                  </div>
                  <div class="fc">
                    <textarea v-model="formData.findings[question.id].root_cause"></textarea>
                  </div>
                  <div class="fc">
                    <label>Responsibility:<input
                        v-model="formData.findings[question.id].corrective_actions"
                        type="text"
                      ></label>
                    <label>&nbsp;Due date:<input
                        v-model="formData.findings[question.id].due_date"
                        type="date"
                      ></label>
                  </div>
                </div>
              </td>
            </tr>
          </template>
        </template>
      </table>
      <button
        type="button"
        @click="sendForm"
      >send</button>
    </form>
  </div>
</template>

<script>
import { filter, values } from "ramda";
import axios from "axios";
export default {
  data: () => ({
    formData: { questions: {}, findings: {} },
    formSectionData: [],
    openSection: [],
    languages: [],
    currentLanguage: "en"
  }),
  created() {
    axios
      .get(
        `${location.protocol}//${location.hostname}${
          process.env.NODE_ENV === "production" ? "" : ":3001"
        }/languages`
      )
      .then(response => {
        this.languages = response.data;
      });
    this.getForms();
  },
  computed: {
    formSection() {
      const sections = this.formSectionData
        .sort(function(a, b) {
          return a.index - b.index;
        })
        .map(section => {
          return {
            ...section,
            questions: section.questions.sort(function(a, b) {
              return a.index - b.index;
            })
          };
        });
      return sections;
    },
    validatedForm() {
      const z = this.formSection.reduce((acc, { id, questions }) => {
        let valid = true;
        const questionsData = questions.reduce((ac, question) => {
          const value = this.formData.questions[question.id];
          let validQuestion = !!value;
          if (validQuestion && value === "No") {
            const finding = this.formData.findings[question.id];
            if (
              !finding ||
              !finding.root_cause ||
              !finding.corrective_actions ||
              !finding.due_date
            ) {
              validQuestion = false;
            }
          }
          if (validQuestion === false) {
            valid = false;
          }
          return {
            ...ac,
            [question.id]: { valid: validQuestion, id: question.id }
          };
        }, {});
        return {
          ...acc,
          [id]: {
            valid,
            id,
            questions: questionsData
          }
        };
      }, {});
      return z;
    }
  },
  watch: {},
  methods: {
    isValid() {
      return (
        values(filter(section => !section.valid, this.validatedForm)).length ===
        0
      );
    },
    isValidSection(id) {
      return this.validatedForm[id].valid;
    },
    getForms() {
      axios
        .get(
          `${location.protocol}//${location.hostname}${
            process.env.NODE_ENV === "production" ? "" : ":3001"
          }/forms/${this.$route.params.id}/data`,
          { params: { hl: this.$route.params.lang } }
        )
        .then(response => (this.formSectionData = response.data));
    },
    changeLang() {
      this.getForms();
    },
    sendForm: function(e) {
      if (this.isValid()) {
        axios.post(
          `${location.protocol}//${location.hostname}${
            process.env.NODE_ENV === "production" ? "" : ":3001"
          }/forms/${this.$route.params.id}/data`,
          this.formData
        );
        this.formData = { questions: {}, findings: {} };
        alert("Data send");
      } else {
        alert("invalid form");
      }
      e.preventDefault();
    },
    isOpen(id) {
      return this.openSection.includes(id);
    },
    open(id) {
      this.openSection = [id];
    },
    isOpenFinding(id) {
      if (this.formData.questions[id] === "No") {
        if (!this.formData.findings[id]) {
          this.formData.findings[id] = {};
        }
        return true;
      }
      return false;
    },
    isSectionFilled(id) {
      if (this.formData.questions[id] === "No") {
        if (!this.formData.findings[id]) {
          this.formData.findings[id] = {};
        }
        return true;
      }
      return false;
    }
  }
};
</script>
